@import url(https://fonts.googleapis.com/css?family=Fira+Mono:400);
html {
  font-size: 62.5%;
  background-color: white;
  width: 100%;
  height: 100vh;
}

*, *:before, *:after {
  box-sizing: border-box;
}

/* <--------------- Nav Bar ----------------> */
#nav-bar-wrapper {
  height: 100px;
  width: 100%;
  align-items: flex-start;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#nav-bar-right {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 20px;
}

.nav-bar {
  font-family: 'Lora', serif;
  color: #323649;
  font-size: 18px;
  text-decoration: none;
}

.nav-bar.current {
  text-decoration: line-through;
}

.site-logo {
  width: 40px;
  height: 40px;
  display: block;
  overflow: visible;
  border: 3px solid #323649;
  position: relative;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  flex-shrink: 0;
  box-sizing: content-box;
  margin: 20px;
  border-radius: 5%;
}

.site-logo .logo-text {
  position: absolute;
  font-family: 'Lora', serif;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(0deg);
          transform: translate(-50%, -50%) rotate(0deg);
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  font-size: 21px;
  font-weight: 500;
  color: #323649;
}

.site-logo:hover {
  opacity: 1;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}
.site-logo:hover .logo-text {
  opacity: 0.9;
  -webkit-transform: translate(-50%, -50%) rotate(-90deg);
          transform: translate(-50%, -50%) rotate(-90deg);
  -webkit-transform-origin: center center;
          transform-origin: center center;
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}
.site-logo:hover #logo-stroke {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transform-origin: center center;
          transform-origin: center center;
  transition: -webkit-transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out;
  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
}
/* <--------------- Home ----------------> */
.homepage {
    font-family: "Fira Mono", monospace;
    color: black;
    font-size: 30px;
    text-decoration: none;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 93vh;
}

.invitation {
    margin-top: 35px;
}

.congratulations {
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 20px;
    text-align: center;
    font-family: "Fira Mono", monospace;
    display: none;
}

.footer {
    margin-top: 10px;
    margin-bottom: 20px;
    font-size: 16px;
    text-align: center;
    font-family: "Fira Mono", monospace;
}

.price {
    font-size: 40px;
    margin-top: 30px;
}

.description {
    font-size: 40px;
}

.minted {
    margin-bottom: 15px;
}

.amount {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: black;
    font-size: 20px;
    margin-top: 30px;
}

.num {
    padding: 10px;
    margin-left: 25px;
    margin-right: 25px;
    margin-top: 0px;
    margin-bottom: 0px;
    border: 2px solid black;
    border-radius: 5px;
    background: none;
    color: black;
    font-size: 25px;
    width: 100px;
    text-align: center;
}

p{
    font-size: 25px;
    margin-top: 0px;
    margin-bottom: 0px;
}

h1 {
    margin: 0px;
}


.mint {
    color: black;
    font-size: 35px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 0px;
    text-decoration: none;
    background: none;
    border: none;
    margin-top: 30px;
    cursor: pointer;
}

.mint:hover {
    font-weight: 700;
}

.minmax {
    background: none;
    border: none;
    font-size: 30px;
    cursor: pointer;
}

.Typewriter__wrapper {
    margin-left: 7px;
    color: black;
}

.emojiWrapper {
    color: black;
}

.Typewriter__cursor {
    font-size: 35px;
    color: black;
    position: relative;
    top: -5px;
}

.noCursor {
    display: none;
}

.emojiContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 35px;
}

#emoji { 
    width: 220px; 
    height: 210px; 
    font-size: 165px;
    text-align: center;
    -webkit-animation-name: float;
            animation-name: float;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.shadow {
    width: 110px;
    height: 15px;
    background: #1c1c1c;		    
    border-radius: 70%;
    opacity: 0.50;
    -webkit-filter: blur(3px);
            filter: blur(3px);
    -webkit-animation-name: animate-shadow;
            animation-name: animate-shadow;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

@-webkit-keyframes float {
    
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    } 100% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
}

@keyframes float {
    
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    } 100% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
}

@-webkit-keyframes animate-shadow {
    
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    } 100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

@keyframes animate-shadow {
    
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    } 100% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}
